.myVideoHead {
    width: calc( 100% - 15px );
    display: -webkit-flex;
    display: flex;
}
.myVideoHead > li:nth-child(1){
    width: 128px;
}
.myVideoHead > li:nth-child(2){
    padding-left: 10px;
    -webkit-flex: 5 1;
            flex: 5 1;
}
.myVideoHead > li:nth-child(3){
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoHead > li:nth-child(4){
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoHead > li:nth-child(5){
    -webkit-flex: 2 1;
            flex: 2 1;
}

.myVideoBody {
    max-height: 600px;
    overflow-y: scroll;
}


.myVideoBody .ant-list-items > .ant-list-item > .ant-list-item-meta {
    padding: 10px;
    -webkit-flex: 5 1;
            flex: 5 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .list-date {
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .list-view-count {
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .ant-list-item-action {
    -webkit-flex: 2 1;
            flex: 2 1;
    margin-left: 0 !important;
}
