
@font-face {
  font-family: 'GongGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-10@1.0/GongGothicMedium.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}


body {
  margin: 0;
  font-family: "GongGothicMedium", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
  font-family: "GongGothicMedium", -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol" !important;
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
}

.app {
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
}

input.error {
  border-color: red;
}


.input-feedback {
  position: absolute;
  color: red;
  bottom: -4px;
  height: 5px;
  /* margin-top: -12px; */
}

.duration {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 4px;
  color: #fff;
  background-color: rgba(17, 17, 17, .8);
  opacity: 0.8;
  padding: 2px 4px;
  border-radius: 2px;
  letter-spacing: 0.5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 12px;
}

.mainTopText {
  text-align: center;
}


.fullBox {
    position: relative;
    width: 100%;
    height: 100vh;
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	-webkit-animation: gradient 15s ease infinite;
	        animation: gradient 15s ease infinite;
}

.fullCenter {
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
}

.buttonSize {
    display: inline-block;
    transition: all 1s;
    height: 0;
    overflow: hidden;
    margin-top: 0px;
}

.buttonSize.active {
    display: inline-block;
    transition: all 1s;
    height: 50px;
    margin-top: 30px;
}



@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}



@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.MPlayer{
    display: -webkit-flex;
    display: flex;
    position: relative;
}

.controlBox {
    background: linear-gradient(to top, rgba(50,50,50,0.8), 20%, rgba(0,0,0,0));
    display: inline-block;
    width: 100%;
    position: absolute;
    padding-top: 20%;
    bottom: 0;
    left: 0;
}

.controlBar {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-size: 2rem;
    position: relative;
    bottom: 4px;
    left: 0;
    width: 98%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: #fff;
}



.controlBar .times {
    display: inline-block;
    font-size: 0.8rem;
}

.progressBar {
    position: absolute;
    width: 100%;
    margin: 0 auto;
    height: 4px;
    top: -10px;
}
.progressBar input {
    position: absolute;
    top:-4px;
    left: 0;
    width: 100%;
}

.myVideoHead {
    width: calc( 100% - 15px );
    display: -webkit-flex;
    display: flex;
}
.myVideoHead > li:nth-child(1){
    width: 128px;
}
.myVideoHead > li:nth-child(2){
    padding-left: 10px;
    -webkit-flex: 5 1;
            flex: 5 1;
}
.myVideoHead > li:nth-child(3){
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoHead > li:nth-child(4){
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoHead > li:nth-child(5){
    -webkit-flex: 2 1;
            flex: 2 1;
}

.myVideoBody {
    max-height: 600px;
    overflow-y: scroll;
}


.myVideoBody .ant-list-items > .ant-list-item > .ant-list-item-meta {
    padding: 10px;
    -webkit-flex: 5 1;
            flex: 5 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .list-date {
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .list-view-count {
    -webkit-flex: 2 1;
            flex: 2 1;
}
.myVideoBody .ant-list-items > .ant-list-item > .ant-list-item-action {
    -webkit-flex: 2 1;
            flex: 2 1;
    margin-left: 0 !important;
}

/* @import '~antd/dist/antd.css'; */

.myPageTop {
  min-width: 1180px;
}

.menu {
  padding: 0 20px;
  /* border-bottom: solid 1px #e8e8e8; */
  overflow: auto;
  /* background-color: #202020; */
}

.menu__logo {
  width: 150px;
  float: left;
}

.menu__logo a {
  display: inline-block;
  font-size: 20px;
  padding: 9px 20px;
}

.menu__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.searchBox {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.menu__container .ant-menu-item {
  padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
}

.menu__container .ant-menu-horizontal {
  border-bottom: none;
}

.menu__container .menu_left {
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.menu__container .menu_rigth {
  /* display: flex; */
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.menu__container .menu_rigth.auth {
  display: -webkit-flex;
  display: flex;
  
}
.menu__container .menu_rigth > ul{
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}


.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
}



@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_left,
  .menu_rigth {
    display: none !important;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}
