/* @import '~antd/dist/antd.css'; */

.myPageTop {
  min-width: 1180px;
}

.menu {
  padding: 0 20px;
  /* border-bottom: solid 1px #e8e8e8; */
  overflow: auto;
  /* background-color: #202020; */
}

.menu__logo {
  width: 150px;
  float: left;
}

.menu__logo a {
  display: inline-block;
  font-size: 20px;
  padding: 9px 20px;
}

.menu__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.searchBox {
  flex: 1;
}

.menu__container .ant-menu-item {
  padding: 0px 5px;
}

.menu__container .ant-menu-submenu-title {
  padding: 10px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
  padding: 10px 15px;
}

.menu__container .ant-menu-horizontal {
  border-bottom: none;
}

.menu__container .menu_left {
  flex-direction: row;
  align-items: center;
  flex: 1;
}

.menu__container .menu_rigth {
  /* display: flex; */
  align-items: center;
  justify-content: flex-end;
  flex: 1;
}
.menu__container .menu_rigth.auth {
  display: flex;
  
}
.menu__container .menu_rigth > ul{
  justify-content: flex-end;
}


.menu__mobile-button {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none !important; /* use of important to overwrite ant-btn */
  background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
  padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
  padding: 14px 24px !important;
}



@media (max-width: 767px) {
  .menu__mobile-button {
    display: inline-block !important;
  }

  .menu_left,
  .menu_rigth {
    display: none !important;
  }

  .menu__logo a {
    margin-left: -20px;
  }

  .menu__container .ant-menu-item,
  .menu__container .ant-menu-submenu-title {
    padding: 1px 20px;
  }

  .menu__logo a {
    padding: 10px 20px;
  }
}